import React, { useState, useCallback } from "react";
import { withRouter, useHistory, useLocation } from "react-router-dom";
import navList from "../../common/navList";
import "./index.less";
import { NavLink, Link } from "react-router-dom";
import { Dropdown, Avatar, Input, Toast, Button } from "@douyinfe/semi-ui";
import {
	IconSearch,
	IconUser,
	IconMailStroked1,
	IconSetting,
	IconQuit,
	IconVerify,
	IconShield,
	IconCrown,
} from "@douyinfe/semi-icons";
import { delUserAction } from "../../store/actions/user";
import { connect } from "react-redux";
import { logout } from "../../services/apis";
import Login from "../Login";
import SubNav from "../SubNav";
const mapStateToProps = (state) => {
	return {
		user: state.user,
	};
};

const mapDispatchToProps = (dispatch) => {
	return {
		delUser: (...args) => dispatch(delUserAction(...args)),
	};
};

function CommonHeader(props) {
	const { user, delUser } = props;
	const history = useHistory();
	const location = useLocation();
	let showSubNav = location.pathname.startsWith('/find'); //  /find 
	const [keyword, setKeyword] = useState(""); // 
	const [visible, setVisible] = useState(false);
	const keywordChange = useCallback((val) => {
		setKeyword(val);
	}, []);

	const searchMusic = useCallback(async () => {
		if (!keyword) {
			Toast.warning({
				content: " /// ",
				duration: 2,
			});
			return;
		}
		history.push(`/search?keywords=${keyword}`);
	}, [keyword, history]);
	//   
	const handleLogout = useCallback(async () => {
		const res = await logout();
		if (res.code === 200) {
			history.push("/find"); // 
			delUser();
			localStorage.removeItem("user");
			Toast.success({
				content: "",
				duration: 2,
			});
		} else {
			Toast.error({
				content: "",
				duration: 2,
			});
		}
	}, []);
	// 
	const toUserHome = useCallback(
		() => {
			history.push(`/user/home?uid=${user.userId}`)
		},
		[],
	)

	const lis = navList.map((nav) => (
		<NavLink to={nav.url} key={nav.url} className="nav-link">
			<li className="nav-item">{nav.text}</li>
		</NavLink>
	));
	return (
		<>
			<div className="nav-wrapper">
				<div className="nav-logo">
					<Link to="/find"></Link>
				</div>
				<ul className="nav-list">{lis}</ul>
				<div className="nav-search">
					<Input
						style={{
							background: "#fff",
							outline: "none",
						}}
						onEnterPress={searchMusic}
						placeholder="///"
						prefix={<IconSearch />}
						value={keyword}
						onChange={keywordChange}
						showClear
					></Input>
				</div>
				{user ? (
					<div className="nav-user">
						<Dropdown
							trigger={"hover"}
							position={"bottom"}
							render={
								<Dropdown.Menu>
									<Dropdown.Item icon={<IconUser />}>
										<Link to={`/user/home?uid=${user.userId}`}></Link>
									</Dropdown.Item>
									<Dropdown.Item icon={<IconMailStroked1 />}>
										<Link to={`/user/event`}></Link>
									</Dropdown.Item>
									<Dropdown.Item icon={<IconCrown />}>
										
									</Dropdown.Item>
									<Dropdown.Item icon={<IconShield />}>
										VIP
									</Dropdown.Item>
									<Dropdown.Item icon={<IconSetting />}>
										
									</Dropdown.Item>
									<Dropdown.Item icon={<IconVerify />}>
										
									</Dropdown.Item>
									<Dropdown.Item icon={<IconQuit />} onClick={handleLogout}>
										
									</Dropdown.Item>
								</Dropdown.Menu>
							}
						>
							<Avatar src={user.avatarUrl} />
						</Dropdown>
					</div>
				) : (
					<Button
						onClick={() => {
							setVisible(true);
						}}
					>
						
					</Button>
				)}
			</div>
			{showSubNav ? <SubNav /> : <div className="sub-nav"></div>}
			<Login
				visible={visible}
				closeModal={() => {
					setVisible(false);
				}}
			></Login>
		</>
	);
}

export default connect(
	mapStateToProps,
	mapDispatchToProps
)(withRouter(CommonHeader));
