import ins from "./request";

// 
export async function getDjPopularList({ limit }) {
    const res = await ins.get("/dj/toplist/popular", {
        params: {
            limit
        }
    });
    return res;
};

// dj banner
export async function getDjBanner() {
    const res = await ins.get("/dj/banner");
    return res;
};

